function container_id(options, host_id) {
  return options.id.prefix + host_id;
}

function ui_html(options, host_id) {
  var html = "\n    <div class=\"".concat(options.class_host, "\" id=\"").concat(container_id(options, host_id), "\">\n        \n        <ul id=\"").concat(options.styled_file_id, "\" class=\"").concat(options.class_uploader_folder, "\"  >\n            <li class=\"").concat(options.class_uploader_folder_img, "\">&nbsp;</li>\n            <li class=\"").concat(options.class_uploader_folder_click, "\">\n                        <a  href=\"#\" class=\"\">\n                    &nbsp; Select Files &nbsp; >> \n                </a>\n            </li>\n        </ul>\n    \n        <div class=\"").concat(options.class_uploader_area, "\">\n            <input class=\"").concat(options.class_uploader, "\" id=\"").concat(options.file_input_id, "\" multiple type=\"file\" onchange='window.").concat(options.upm_global, ".selectFiles(this)' class=\"hidden\">\n\n            <p class=\"").concat(options.class_drag_text, "\">or drag and drop them here.</p>\n        \n        </div>\n\n        <div id=\"").concat(options.file_list_id, "\" class=\"\">\n        </div>\n    </div>\n    ");
  return html;
}

function click_file(file_input_id) {
  document.getElementById(file_input_id).click();
}

function attach_chunk_ui(div, options, host_id) {
  var chunk_container = document.getElementById(container_id(options, host_id));

  if (null === chunk_container) {
    div.innerHTML = ui_html(options, div.id);
    var styled_file = document.getElementById(options.styled_file_id);
    styled_file.addEventListener('click', function () {
      return click_file(options.file_input_id);
    });
  }
}

function file_ui(id, options) {
  var div = document.createElement('ul');
  var att = document.createAttribute('id');
  att.value = id;
  div.setAttributeNode(att);
  var cls = document.createAttribute('class');
  cls.value = options.class_uploader_file_list;
  div.setAttributeNode(cls);
  var file_ui_template = "    <li><span>&nbsp;</span></li>\n\n                                <li class=\"\"> \n                                    <a class=\"".concat(options.class_uploader_button, "\"><span class=\"").concat(options.class_btn.play, "\"></span> <span>start</span></a>\n                                    <a class=\"").concat(options.class_uploader_button, "\"><span class=\"").concat(options.class_btn.quit, "\"></span> <span>quit</span></a>\n                                    <a class=\"").concat(options.class_uploader_button, "\"><span class=\"").concat(options.class_btn.options, "\"></span> <span>options</span></a>\n                                </li>\n\n                                <li  class=\"").concat(options.class_uploader_percentage, "\">\n                                    <div class=\"\" style=\"width: 0%;\"></div>\n                                        <span></span>\n                                </li>\n\n                                <li class=\"").concat(options.class.hidden, "\">\n                                    <div class=\"").concat(options.class.notify.main, "\"></div>\n                                </li>\n                            ");
  div.innerHTML = file_ui_template;
  return div;
}

function update_start_ui(play, my_pause, options) {
  if (!play) {
    my_pause.classList.remove(options.class_btn.pause);
    my_pause.classList.add(options.class_btn.play);
    my_pause.nextElementSibling.innerText = 'start';
  } else {
    my_pause.classList.remove(options.class_btn.play);
    my_pause.classList.add(options.class_btn.pause);
    my_pause.nextElementSibling.innerText = 'pause';
  }
}

function get_children(div) {
  var info = div.children[0];
  var txt = info.children[0];
  var buttons = div.children[1];
  var progress = div.children[2];
  var notify = div.children[3];
  var notify_txt = notify.children[0];
  var $pause = buttons.children[0];
  var $quit = buttons.children[1];
  var $opt = buttons.children[2];
  var my_pause = $pause.children[0];
  return {
    info: info,
    txt: txt,
    buttons: buttons,
    progress: progress,
    notify: notify,
    notify_txt: notify_txt,
    $pause: $pause,
    $quit: $quit,
    $opt: $opt,
    my_pause: my_pause
  };
}

function error_ui(err, options, id) {
  var div = document.getElementById(id);

  if (null === div) {
    return;
  }

  var children = get_children(div);
  children.notify_txt.innerText = 'Error: ' + err.message;
  update_start_ui(false, children.my_pause, options);
  children.notify.classList.toggle(options.class.hidden);
  children.notify.children[0].classList.add(options.class.notify.error);
}

function complete_ui(options, id) {
  var div = document.getElementById(id);

  if (null === div) {
    return;
  }

  var children = get_children(div);
  children.notify_txt.innerText = 'Upload Completed.';
  children.notify.classList.toggle(options.class.hidden);
  children.notify.previousElementSibling.classList.toggle(options.class.hidden);
  children.notify.previousElementSibling.previousElementSibling.classList.toggle(options.class.hidden);
  children.notify.children[0].classList.remove(options.class.notify.error);
  children.notify.children[0].classList.add(options.class.notify.success);
}

function quit_ui(options, id) {
  var div = document.getElementById(id);
  var children = get_children(div);
  children.notify_txt.innerText = 'Upload Quitted.';
  children.notify.classList.toggle(options.class.hidden);
  children.notify.previousElementSibling.classList.toggle(options.class.hidden);
  children.notify.previousElementSibling.previousElementSibling.classList.toggle(options.class.hidden);
  children.notify.children[0].classList.add(options.class.notify.quitted);
}

function play_pause(options, id) {
  var myself = window[options.upm_global].uploader[id];
  var div = document.getElementById(id);
  var children = get_children(div);

  if (myself.paused()) {
    //myself.resume();
    children.notify.classList.add(options.class.hidden);
  }

  update_start_ui(!myself.paused(), children.my_pause, options);
}

function add_file_ui(id, options) {
  var myself = window[options.upm_global].uploader[id];
  var file_list = document.getElementById(options.file_list_id);
  var div = document.getElementById(id);

  if (null === div) {
    file_list.appendChild(file_ui(id, options));
    div = document.getElementById(id);
    var children = get_children(div);
    children.$pause.addEventListener('click', function () {
      if (myself.paused()) {
        myself.resume();
      } else {
        myself.pause();
      }

      play_pause(options, id);
    });
    children.$opt.addEventListener('click', function () {
      options.options_function(myself);
    });
    children.$quit.addEventListener('click', function () {
      myself.quit();
      quit_ui(options, id);
    });
    children.txt.innerHTML = myself.name();
    if (myself.status === 'started' || myself.status === 'paused') play_pause(options, id);
    if (myself.status === 'quitted') quit_ui(options, id);
    if (myself.status === 'completed') complete_ui(options, id);
    if (myself.status === 'error') error_ui(myself._err, options, id);
  }
}

function new_file(id, options) {
  var myself = window[options.upm_global].uploader[id];
  add_file_ui(id, options);
  myself.on('progress', function (num) {
    var div = document.getElementById(id);

    if (null === div) {
      return;
    }

    var children = get_children(div);
    var d = new Number(num);
    var x = new String(d.toFixed(0)) + '%';
    var s = new String(d.toFixed(2)) + '%';
    children.progress.children[0].style.width = x;
    children.progress.children[1].innerHTML = s;
  });
  myself.on('completed', function () {
    complete_ui(options, id);
  });
  myself.on('error', function (err) {
    error_ui(err, options, id);
  });
}

function onerror(err, id, options) {
  console.error(err.message, err.stack, id, JSON.stringify(options, null, 4));
}
